        </div>
        <div v-else class="user-info">
          شما
        </div>
      </div>
      <div class="report-content">
        <MediaSlider v-if="report?.media?.length>0" :medias="report.media" />
        <h3 v-if="report?.description">{{ report.description }}</h3>
        <div class="times">
          <small v-if="report?.created_at">ثبت: {{ moment(report.created_at).format('jYYYY/jMM/jDD') }}</small>
          <small v-if="report?.run_time">ملاقات {{ moment(report.run_time).format('jYYYY/jMM/jDD') }}</small>
        </div>
      </div>
    </section>
    <div v-if="isModalOpen" class="modal-overlay">
      <div class="modal">
        <h3>پیوست گزارش</h3>
        <UploaderManyMedia 
          :url="'report_media/'"
          :toAction="'report'"
          v-model="mediaIds"
          @done="handleUploadResult" 
        />
        <form @submit.prevent="submitForm">
          <textarea v-model="description" rows="4" placeholder="توضیحات..." />
          <div class="modal-actions">
            <button type="submit">ثبت</button>
            <button @click.prevent="isModalOpen = false">بستن</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script setup>
  import { ref , defineProps , watch , computed } from 'vue'
  import { useCartableStore } from '@/stores/cartable'
  import { usePollingWithCompare } from '@/composables/usePollingWithCompare'
  import MediaSlider from '@/components/tooles/media/MediaSlider.vue'
  import UploaderManyMedia from '@/components/tooles/upload/UploaderManyMedia.vue'
  import moment from 'moment-jalaali'
  const props = defineProps({ id: Number })
  const store = useCartableStore()
  const item = ref(null)
  const isModalOpen = ref(false)
  const description = ref('')
  const mediaIds = ref([])
  const isFirstLoad = ref(true)
  const fetchSingleItem = async () => {
    if (isFirstLoad.value) store.loading = true
    const res = await store.getCartableById(props.id)
    if (isFirstLoad.value) {
      store.loading = false
      isFirstLoad.value = false
    }
    return res
  }
  const patchReactiveObject = (target, source) => {
    if (!target || typeof target !== 'object') return
    for (const key in source) {
      const srcVal = source[key]
      const tgtVal = target[key]
      if (Array.isArray(srcVal)) {
        target[key] = [...srcVal]
      } else if (typeof srcVal === 'object' && srcVal !== null) {
        if (!tgtVal || typeof tgtVal !== 'object') target[key] = {}
        patchReactiveObject(target[key], srcVal)
      } else {
        target[key] = srcVal
      }
    }
  }
  usePollingWithCompare(fetchSingleItem, {
    intervalMs: 6000,
    runOnStart: true,
    onChange: (data) => {
      if (!item.value) {
        item.value = data
      } else {
        patchReactiveObject(item.value, data)
      }
    }
  })
  const openModal = () => {
    const r = report.value
    description.value = r?.description || ''
    mediaIds.value = Array.isArray(r?.media) ? r.media.map(m => m.id) : []
    isModalOpen.value = true
  }
  const handleUploadResult = (uploaded) => {
    mediaIds.value = uploaded.map(m => m.id)
  }
  const submitForm = async () => {
    const success = await store.updateReport(props.id, description.value, mediaIds.value)
    if (success) {
      item.value = await store.getCartableById(props.id)
      if (!item.value) return
      isModalOpen.value = false
    }
  }
  watch(() => props.id, async (newId) => {
    if (newId) {
      item.value = await fetchSingleItem()
    }
  }, { immediate: true })

  const report = computed(() => item.value?.reports[0] || {})
  const reporter = computed(() => report.value.reporter || {})
</script>
