<template>
  <div class="user-settings">
    <h2>تنظیمات کاربر</h2>

    <div v-if="!userStore.isLoaded">
      در حال بارگذاری اطلاعات...
    </div>

    <div v-else-if="userStore.users.length>0">
      <div class="user-info" v-for="user in userStore.users" :key="user.id">
        <img :src="user.image || defaultAvatar" alt="User Avatar" class="avatar" />
        <p><strong>نام کامل:</strong> {{ user.fullName }}</p>
        <p><strong>موبایل:</strong> {{ user.mobile }}</p>
        <p><strong>موجودی کیف پول:</strong> {{ user.wallet }} تومان</p>
        <p><strong>امتیاز توکن:</strong> {{ user.tokenScore }}</p>
        <p><strong>اثر انگشت فعال:</strong> {{ user.finger ? 'بله' : 'خیر' }}</p>
        <p><strong>قوانین:</strong> {{ user.rule ? 'فعال' : 'غیرفعال' }}</p>
      </div>
      <div class="actions">
        <button @click="refreshUser">بروزرسانی اطلاعات</button>
      </div>
    </div>
    <div class="error" v-else>
        کاربری موجود نیست
    </div>
  </div>
</template>
<script setup>
    import { useUserStore } from '@/stores/user'
    import { onMounted, ref } from 'vue'
    const userStore = useUserStore()
    const defaultAvatar = ref('/images/default-avatar.png')
    const refreshUser = () => {
      userStore.fetchUserInfo()
    }
    onMounted(() => {
        userStore.fetchUserInfo()
    })
</script>