          </div>
          <MediaSlider v-if="report.media?.length>0" :medias="report.media" />
          <p v-if="report.description">{{ report.description }}</p>
          <p v-if="report.run_time">📅 تاریخ ملاقات {{ moment(report.run_time).format('jYYYY/jMM/jDD') }}</p>
          <RouterLink class="send" v-if="report.id" :to="{ path: `/show-cartable/${report.id}` }">
            مشاهده گزارش
          </RouterLink>
        </div>
      </div>
    </div>
    <div v-if="canLoadMore" ref="loadMoreTrigger" class="load-trigger">
      در حال بارگذاری موارد بیشتر...
    </div>
  </div>
  <div v-else class="no-data">
    شما هیچ گزارشی در کارتابل خود ندارید
  </div>
</template>
<script setup>
  import moment from 'moment-jalaali'
  import { computed, ref } from 'vue'
  import { useCartableStore } from '@/stores/cartable'
  import { usePollingWithCompare } from '@/composables/usePollingWithCompare'
  import { useInfiniteScroll } from '@/composables/useInfiniteScroll'
  import MediaSlider from '@/components/tooles/media/MediaSlider.vue'
  const store = useCartableStore()
  const all = ref([])
  const hasLoadedOnce = ref(false)
  const {
    loading,
    canLoadMore,
    loadMoreTrigger,
    setupObserver,
  } = useInfiniteScroll(async ({ offset, limit = 5 }) => {
    if (!hasLoadedOnce.value) {
      await store.fetchCartables()
      hasLoadedOnce.value = true
    }
    const newItems = store.allItems.slice(offset, offset + limit)
    const existingIds = new Set(all.value.map(i => i.id))
    const uniqueItems = newItems.filter(i => !existingIds.has(i.id))
    all.value.push(...uniqueItems)
    return {
      items: uniqueItems,
      has_more: offset + newItems.length < store.allItems.length,
    }
  })
  setupObserver()
  usePollingWithCompare(async () => {
    await store.fetchCartables()
    return [...store.allItems]
  }, {
    intervalMs: 10000,
    runOnStart: true,
    isDifferent: (oldList, newList) => {
      return JSON.stringify(oldList) !== JSON.stringify(newList)
    },
    onChange: (newData) => {
      store.allItems = newData
      all.value = [...newData]
    }
  })
  const items = computed(() => all.value)
</script>
<style scoped>
  .tiny-loader {
    width: 20px;
    height: 20px;
    border: 2px solid #ccc;
    border-top-color: #333;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin: 10px auto;
  }
  @keyframes spin {
    to { transform: rotate(360deg); }
  }
  .loading {
    font-size: 1.1rem;
    padding: 2rem;
    text-align: center;
    color: #555;
  }
  .no-data{
    padding: 20px;
    text-align: center;
    background: #9a0000;
    border-radius: 10px;
    box-shadow: 0 0 10px grey;
    color: white;
    font-size: larger;
    font-weight: bold;
  }
  .cartable-inner {
    direction: rtl;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  .cartable {
    background-color: #ffffff;
    padding: 1rem 1.2rem;
    border-radius: 10px;
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.1);
  }
  .user-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;
  }
  .user-info div {
    display: flex;
    gap: 0.75rem;
    align-items: center;
  }
  .user-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid #ccc;
  }
  .user-info svg {
    width: 20px;
    height: 20px;
  }
  .user-info strong {
    font-weight: bold;
    font-size: 1rem;
    color: #333;
  }
  .user-info small {
    color: #666;
    font-size: 0.9rem;
  }
  .news-description {
    font-size: 1rem;
    margin: 0.5rem 0;
    color: #444;
  }
  .address {
    font-size: 0.9rem;
    color: #555;
    margin-bottom: 0.5rem;
    word-break: break-all;
  }
  button {
    display: block;
    margin: 1rem auto 0;
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 0.6rem 1.4rem;
    font-size: 0.95rem;
    border-radius: 6px;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  button:hover {
    background-color: #0056b3;
  }
  .send {
    display: inline-block;
    width: 100%;
    margin-top: .5rem;
    padding: 10px;
    text-decoration: none;
    background: #007bff;
    font-weight: 500;
    border-radius: 10px;
    text-align: center;
    color: white;
  }
  .send:hover {
    opacity: 0.6;
  }
  .report-item {
    margin-top: 15px;
  }
</style>