<script setup>
  import { defineProps, defineEmits } from 'vue'
  import BaseModal from '@/components/tooles/modal/BaseModal.vue'
  import MediaSlider from '@/components/tooles/media/MediaSlider.vue'
  const props = defineProps({
    show: Boolean,
    event: Object
  })
  defineEmits(['close'])
  const truncateText = (text, max = 50) => {
    if (!text) return ''
    return text.length > max ? text.slice(0, max) + '...' : text
  }
  const getStatus = (status) => {
    switch (status) {
      case 'seen': return 'در حال پیگیری'
      case 'checking': return 'در حال بررسی'
      case 'done': return 'انجام شده'
      default: return 'نامشخص'
    }
  }
</script>
